<template>
  <div class="wrapper">
    <Grid style="margin: 24px 0">
      <div></div>
      <div>
        <span v-if="!stuInfo.stuBindPhone"> -- </span>
        <tal-decode-text v-else
                         :encode-text="stuInfo.stuBindPhone"
                         type="phone"
                         :uid="stuDetail.uid">
        </tal-decode-text>
      </div>
      <div></div>
      <div>{{ stuInfo.stuGradeName || '--'}}</div>
      <div></div>
      <div class="can-edit">
        <span>{{ stuInfo.schoolName || '--'}}</span>
        <Edit v-if="stuDetail.uid" @click.native="$emit('editStu')"></Edit>
      </div>
      <div></div>
      <div>
        <div v-if="!editStatus.classSituationId" class="can-edit">
          <span>{{ classSituationMap[familyData.innerSchoolInfoResp.classSituationId] || '--'}}</span>
          <Edit v-if="stuDetail.uid" @click.native="editStatus.classSituationId = true"></Edit>
        </div>
        <div v-else class="can-edit">
          <Select v-model="familyData.innerSchoolInfoResp.classSituationId">
            <Option v-for="(v, k) in classSituationMap" :value="k" :key="k">{{v}}</Option>
          </Select>
          <Button type="primary" size="small" style="margin-left: 10px" @click="updateValue('classSituationId')"></Button>
        </div>
      </div>
      <div>
        <span></span>
        <Tooltip transfer placement="right" theme="light" style="margin-right: 25px">
          <div slot="content">
            {{stuInfo.stuRegistName || '--'}}
          </div>
          <Icon type="ios-help-circle-outline" :size="16"/>
        </Tooltip>
      </div>
      <div>
        <div>{{stuInfo.stuRegistName || '--'}}</div>
        <div style="font-size: 12px;font-weight: bold">
          {{ getRemarks() }}
        </div>
      </div>
      <div></div>
      <div>{{ stuInfo.stuCreateDate  || '--'}}</div>
      <div></div>
      <div>{{ stuInfo.completeTime  || '--'}}</div>
      <div></div>
      <div>{{ stuInfo.stuCreaterName  || '--'}}</div>
      <div></div>
      <div>{{ stuInfo.areaName  || '--'}}</div>
      <div></div>
      <div>{{ stuDetail.bindingName  || '--'}}</div>
      <div></div>
      <div>{{ stuInfo.studentCrmFeature.belongDeptName  || '--'}}</div>
      <div></div>
      <div>{{ stuInfo.studentCrmFeature.consultant  || '--'}}</div>
      <div></div>
      <div>{{ isNewOld ? '' : ''}}</div>
      <div></div>
      <div>
        <span>{{ getWxStatus(relatedAssistantWx) }}</span>
        <a
          v-if="relatedAssistantWx.nextStep === 0"
          href="javascript:void(0)"
          @click="addWxClick"
        >
          <Icon size="24" type="md-person-add" />&nbsp;
        </a>
        <a
          v-if="relatedAssistantWx.nextStep === 1"
          href="javascript:void(0)"
          @click="toTiance"
        >
          <span style="color: #1fd037;margin-left: 5px" class="iconfont icon-weixin"></span>
        </a>
      </div>
      <div></div>
      <div>
        <div v-if="!editStatus.childrenNum" class="can-edit">
          <span>{{ familyData.familyInfoResp.childrenNum || '--'}}</span>
          <Edit v-if="stuDetail.uid" @click.native="editStatus.childrenNum = true"></Edit>
        </div>
        <div v-else class="can-edit">
          <InputNumber v-model="familyData.familyInfoResp.childrenNum" :min="0" placeholder=""></InputNumber>
          <Button type="primary" size="small" style="margin-left: 10px" @click="updateValue('childrenNum')"></Button>
        </div>
      </div>
      <div>IPS</div>
      <div>{{stuInfo.ipsScore || (stuInfo.ipsScore === 0 ? 0 : '--')}}</div>
      <div></div>
      <div style="grid-column-start: 2;grid-column-end: 5;">
        <div style="color: #3B7EFF;cursor: pointer;" v-if="familyData.innerSchoolInfoResp.applyClassDtoList && familyData.innerSchoolInfoResp.applyClassDtoList.length" type="text" @click="viewApplyClass"></div>
        <div v-else class="can-edit">
          <span>--</span>
          <Edit v-if="stuDetail.uid" @click.native="viewApplyClass"></Edit>
        </div>
      </div>
      <div> & </div>
      <div style="grid-column-start: 6;grid-column-end: 9;">
        <div v-if="!editStatus.hobby" class="can-edit">
          <span>{{ familyData.innerSchoolInfoResp.hobby || '--'}}</span>
          <Edit v-if="stuDetail.uid" @click.native="editStatus.hobby = true"></Edit>
        </div>
        <div v-else class="can-edit">
          <Input v-model="familyData.innerSchoolInfoResp.hobby" placeholder=""></Input>
          <Button type="primary" size="small" style="margin-left: 10px" @click="updateValue('hobby')"></Button>
        </div>
      </div>
      <div> & </div>
      <div style="grid-column-start: 2;grid-column-end: 5;">
        <div v-if="!editStatus.learnProject" class="can-edit">
          <span>{{ familyData.familyInfoResp.learnProject || '--'}}</span>
          <Edit v-if="stuDetail.uid" @click.native="editStatus.learnProject = true"></Edit>
        </div>
        <div v-else class="can-edit">
          <Input v-model="familyData.familyInfoResp.learnProject" placeholder=""></Input>
          <Button type="primary" size="small" style="margin-left: 10px" @click="updateValue('learnProject')"></Button>
        </div>
      </div>
      <div> & </div>
      <div style="grid-column-start: 6;grid-column-end: 9;">
        <div v-if="!editStatus.familyEducation" class="can-edit">
          <span>{{ familyData.familyInfoResp.familyEducation || '--'}}</span>
          <Edit v-if="stuDetail.uid" @click.native="editStatus.familyEducation = true"></Edit>
        </div>
        <div v-else class="can-edit">
          <Input v-model="familyData.familyInfoResp.familyEducation" placeholder=""></Input>
          <Button type="primary" size="small" style="margin-left: 10px" @click="updateValue('familyEducation')"></Button>
        </div>
      </div>
    </Grid>
    <Modal
      :mask-closable="false"
      title=""
      width="560"
      v-model="showAddWxModal"
      :footer-hide="true"
    >
      <AddWx
        v-if="showAddWxModal"
        :uid="stuDetail.uid"
        :assistantWxList="assistantWxList"
        @add-success="addSuccess"
        :encoded-phone="stuInfo.stuBindPhone"
      ></AddWx>
    </Modal>
    <Drawer title="" :closable="false" v-model="applyClassShow" :width="700">
      <Table :columns="cols" :data="familyData.innerSchoolInfoResp.applyClassDtoList">
      </Table>
      <Button style="width: 100%;margin-top: 16px" type="dashed" @click="applyClassAdd"><Icon type="md-add" :size="16" style="margin-right: 10px"/></Button>
    </Drawer>
    <Drawer title="" v-model="editApplyClassShow" :width="350">
      <div>
        <Form ref="enterClassForm" :model="formData" :rules="ruleValidate" label-position="top">
          <FormItem label="" prop="trainingInstitutions">
            <Input v-model="formData.trainingInstitutions" style="width: 320px" :maxlength="40" placeholder=""></Input>
          </FormItem>
          <span class="input-count" >{{formData.trainingInstitutions ? formData.trainingInstitutions.length : 0}}/40</span>
          <FormItem label="" prop="subject">
            <Input v-model="formData.subject" style="width: 320px" :maxlength="40" placeholder=""></Input>
          </FormItem>
          <span class="input-count">{{formData.subject ? formData.subject.length : 0}}/40</span>
          <FormItem label="" prop="signUpTime">
            <Input v-model="formData.signUpTime" style="width: 320px" :maxlength="40" placeholder="YYY/MM/DD-YYY/MM/DD"></Input>
          </FormItem>
          <span class="input-count">{{formData.signUpTime ? formData.signUpTime.length : 0}}/40</span>
          <FormItem label="" prop="classTime">
            <Input v-model="formData.classTime" style="width: 320px" :maxlength="40" placeholder=" HH:MM"></Input>
          </FormItem>
          <span class="input-count">{{formData.classTime ? formData.classTime.length : 0}}/40</span>
        </Form>
      </div>
      <div style="text-align: center">
        <Button @click="saveConfirm" type="primary" style="margin-left: 10px"></Button>
        <Button @click="editApplyClassShow = false"></Button>
      </div>
    </Drawer>
  </div>
</template>

<script>
import { queryStuFamilyRecord, updateStudentArchive } from '@/api/crmStudent'
import { getWxStatus, genWakeup, bindRelate } from '@/api/wxmanage'
import Grid from './grid'
import AddWx from './addWx'
import Edit from '_c/archives/edit'
const form = {
  classTime: '',
  signUpTime: '',
  subject: '',
  trainingInstitutions: ''
}
export default {
  name: 'stuInfo',
  components: {
    Grid,
    AddWx,
    Edit
  },
  props: {
    stuDetail: Object,
    stuInfo: Object,
    isNewOld: Boolean,
    mofangData: Array
  },
  data () {
    return {
      editStatus: {
        classSituationId: false,
        childrenNum: false,
        hobby: false,
        learnProject: false,
        familyEducation: false
      },
      familyData: {
        familyInfoResp: {},
        innerSchoolInfoResp: {
          applyClassDtoList: []
        }
      },
      classSituationMap: {
        '0': '',
        '1': '',
        '2': ''
      },
      isChildrenNumEdit: false, // 
      isClassTypeEdit: false, // 
      showAddWxModal: false, // 
      relatedAssistantWx: {}, // 
      assistantWxList: {}, // 
      wxStatus: null, //  -1 0 1 
      isLoading: false,
      tianceUrl: '', // URL
      applyClassShow: false,
      isEditApplyClass: false,
      cols: [
        {
          title: '',
          key: 'trainingInstitutions',
          align: 'center'
        },
        {
          title: '',
          key: 'subject',
          align: 'center'
        },
        {
          title: '',
          key: 'signUpTime',
          align: 'center'
        },
        {
          title: '',
          key: 'classTime',
          align: 'center'
        },
        {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  cursor: 'pointer'
                },
                on: {
                  click: () => {
                    this.applyClassEdit(params.row)
                  }
                }
              }, ''),
              h('span', {
                style: {
                  margin: '0 5px'
                }
              }, '|'),
              h('a', {
                style: {
                  cursor: 'pointer'
                },
                on: {
                  click: () => {
                    this.applyClassDelete(params.row)
                  }
                }
              }, '')
            ])
          }
        }
      ],
      formData: Object.assign({}, form),
      ruleValidate: {
        trainingInstitutions: [
          { required: true, message: '', trigger: 'blur' }
        ],
        subject: [
          { required: true, message: '', trigger: 'blur' }
        ],
        signUpTime: [
          { required: true, message: '', trigger: 'blur' }
        ],
        classTime: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editApplyClassShow: false
    }
  },
  methods: {
    getRemarks () {
      let remarks = ''
      if (this.stuInfo.sceneMessage) {
        if (this.stuInfo.sceneMessage.sceneType) {
          remarks += this.stuInfo.sceneMessage.sceneType
        }
        if (this.stuInfo.sceneMessage.secondaryType) {
          remarks += ' / ' + this.stuInfo.sceneMessage.secondaryType
        }
        if (this.stuInfo.sceneMessage.activityName) {
          remarks += ' / ' + this.stuInfo.sceneMessage.activityName
        }
      }
      return remarks
    },
    updateValue (type) {
      let req = {
        studentUid: this.stuDetail.uid
      }
      switch (type) {
        case 'hobby':
          req = Object.assign(req, {
            type: 1,
            hobby: this.familyData.innerSchoolInfoResp.hobby
          })
          break
        case 'classSituationId':
          req = Object.assign(req, {
            type: 1,
            classSituationId: this.familyData.innerSchoolInfoResp.classSituationId
          })
          break
        case 'learnProject':
          req = Object.assign(req, {
            type: 2,
            learnProject: this.familyData.familyInfoResp.learnProject
          })
          break
        case 'familyEducation':
          req = Object.assign(req, {
            type: 2,
            familyEducation: this.familyData.familyInfoResp.familyEducation
          })
          break
        case 'childrenNum': {
          req = Object.assign(req, {
            type: 2,
            childrenNum: this.familyData.familyInfoResp.childrenNum
          })
        }
      }
      this.updateStudentArchive(req, type)
    },
    updateStudentArchive (req, type) {
      updateStudentArchive(req).then(() => {
        this.queryStuFamilyRecordHandler()
        this.isChildrenNumEdit = false
        this.editApplyClassShow = false
        if (type) {
          this.editStatus[type] = false
        }
        this.$Message.success({ content: '' })
      }).catch((err) => {
        this.queryStuFamilyRecordHandler()
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    queryStuFamilyRecordHandler () {
      queryStuFamilyRecord({
        studentUid: this.stuDetail.uid,
        cityCode: this.stuDetail.cityCode
      }).then(data => {
        if (!data.innerSchoolInfoResp.applyClassDtoList) {
          data.innerSchoolInfoResp.applyClassDtoList = []
        }
        if (data.familyInfoResp.childrenNum) {
          data.familyInfoResp.childrenNum = Number(data.familyInfoResp.childrenNum)
        }
        this.familyData = data
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    async getWxList () {
      try {
        const params = {
          manualFlag: 1
        }
        const data = await getWxStatus(params)
        this.assistantWxList = data.assistantWxList
        this.wxStatus = data.wxStatus
      } catch (error) {
        this.$Message.error({ content: '' + error.message, duration: 5 })
      }
    },
    //  click
    async addWxClick () {
      await this.getWxList()
      if (this.assistantWxList.length && this.assistantWxList.find(a => a.loginStatus === 1)) {
        this.showAddWxModal = true
      } else {
        return this.$Message.warning({ content: '', duration: 5 })
      }
    },
    async initView () {
      this.queryStuFamilyRecordHandler()
      if (this.$haveAuthority('roleRelativeData', '/SPLAT/tiance')) {
        await this.getWxList()
        if (this.assistantWxList.length) {
          await this.getBindRelate()
        } else {
          this.relatedAssistantWx = {
            assistantWxBindStatus: {
              assistantId: '',
              studentNickName: '',
              studentWxId: '',
              status: -1
            },
            nextStep: 0
          }
        }
      }
    },
    // 
    getWxStatus (relatedAssistantWx) {
      if (relatedAssistantWx.assistantWxBindStatus === undefined) return '--'
      switch (relatedAssistantWx.assistantWxBindStatus.status) {
        case -1: return ''
        case 0: return ''
        case 1: return ''
        default: return '--'
      }
    },
    // 
    toTiance () {
      this.genWakeup()
      this.buriedPoint('send')
    },
    // 
    buriedPoint (type) {
      let trackId = ''
      switch (type) {
        case 'send': trackId = '15-2839'; break // 
        case 'addWx': trackId = '15-2841'; break // 
        default: trackId = ''
      }
      if (trackId) {
        XesAnalytics.track(trackId, {
          adviser_empNo: this.$store.state.roleRelativeData.roleInfo.userWorkCode
        })
      }
    },
    // 
    async genWakeup () {
      try {
        if (this.relatedAssistantWx.assistantWxBindStatus === undefined) {
          return
        }
        const assistantId = this.relatedAssistantWx.assistantWxBindStatus.assistantId
        let offLine = 0
        this.assistantWxList.forEach(item => {
          if (item.assistantId === assistantId && item.loginStatus === 0) {
            offLine = 1
          }
        })
        const params = {
          assistantId: assistantId,
          fragment: 'universalmessaging/im',
          targetId: this.relatedAssistantWx.assistantWxBindStatus.studentWxId,
          targetName: this.relatedAssistantWx.assistantWxBindStatus.studentNickName,
          offLine
        }

        const data = await genWakeup(params)
        this.tianceUrl = data.url
        this.openUrlByNewWindow(this.tianceUrl)
      } catch (error) {
        this.$Message.error({ content: '' + error.message, duration: 5 })
      }
    },
    // 
    async getBindRelate () {
      try {
        const params = {
          uid: this.stuDetail.uid,
          assistantIds: this.assistantWxList.map(item => item.assistantId).join(',')
        }
        const data = await bindRelate(params)
        this.relatedAssistantWx = data || {}
      } catch (error) {
        this.$Message.error({ content: '' + error.message, duration: 5 })
      }
    },
    openUrlByNewWindow (url) {
      const aEle = document.createElement('a')
      aEle.href = url
      aEle.target = '_blank'
      const e = document.createEvent('MouseEvents')
      e.initEvent('click', true, true)
      aEle.dispatchEvent(e)
    },
    addSuccess () {
      this.relatedAssistantWx.assistantWxBindStatus.status = 0
      this.showAddWxModal = false
      this.buriedPoint('addWx')
    },
    viewApplyClass () {
      this.applyClassShow = true
    },
    applyClassEdit (row) {
      this.editRowIndex = row._index
      this.isEditApplyClass = true
      this.formData = Object.assign({}, row)
      this.editApplyClassShow = true
    },
    applyClassDelete (item) {
      this.$Modal.confirm({
        title: '',
        content: ` <span style="color: green">${item.trainingInstitutions} ${item.subject}</span> `,
        onOk: () => {
          console.log('')
          let req = {
            studentUid: this.stuDetail.uid,
            type: 1, // 12
            applyClassList: [...this.familyData.innerSchoolInfoResp.applyClassDtoList]
          }
          req.applyClassList.splice(item._index, 1)
          this.updateStudentArchive(req)
        }
      })
    },
    saveConfirm () {
      this.$refs['enterClassForm'].validate((valid) => {
        if (valid) {
          // 
          if (this.isEditApplyClass) {
            let req = {
              type: 1, // 12
              studentUid: this.stuDetail.uid,
              applyClassList: [...this.familyData.innerSchoolInfoResp.applyClassDtoList]
            }
            req.applyClassList[this.editRowIndex] = this.formData
            this.updateStudentArchive(req)
          } else {
            let req = {
              type: 1, // 12
              studentUid: this.stuDetail.uid,
              applyClassList: [...this.familyData.innerSchoolInfoResp.applyClassDtoList]
            }
            req.applyClassList.push(this.formData)
            this.updateStudentArchive(req)
          }
        }
      })
    },
    applyClassAdd () {
      this.formData = Object.assign({}, form)
      this.isEditApplyClass = false
      this.editApplyClassShow = true
    }
  }
}
</script>
<style scoped lang="less">
  .wrapper {
    /deep/ .ivu-icon {
      cursor: pointer;
    }
    .can-edit {
      display: flex;
      justify-content: space-between;
    }
  }
  .input-count {
    text-align: right;
    color: #909399;
    font-size: 12px;
    width: 320px;
    display: inline-block;
    margin-bottom: 10px;
  }
  .ivu-form-item{
    margin-bottom: 0;
  }
  /deep/ .ivu-drawer-header-inner {
    font-size: 20px;
  }
  /deep/ .ivu-table-header .ivu-table-cell {
    color: #90939a !important;
    padding: 10px 16px !important;
    font-weight: normal;
  }

  /deep/ .ivu-table-cell {
    padding: 10px 16px !important;
    color: rgba(0, 0, 0, 0.85);
  }
</style>
