<template>
  <Row class="wrapper" type="flex" align="middle">
    <Col span="12" class="stu-label">
      <span class="label"></span>
      <Tooltip
        placement="right"
        theme="light"
        style="margin-right: 25px"
        :max-width="700"
      >
        <div slot="content">
          <p><strong></strong>5</p>
          <p>
            <strong></strong
            >APP
          </p>
          <p>
            <strong>S/A/B/C/D</strong
            >
          </p>
          <p>
            <strong></strong
            >
          </p>
        </div>
        <Icon
          type="ios-help-circle-outline"
          color="rgba(0, 0, 0, 0.45)"
          :size="16"
        />
      </Tooltip>
      <div class="row_block">
        <Tag
          class="flex"
          v-for="label in userLabelList"
          color="blue"
          :key="label.labelId"
          >{{ label.label }}</Tag
        >
        <Tag class="flex" v-if="bringNewAbility" color="blue"></Tag>
        <Tag class="flex" v-if="highTransformPotential" color="blue"
          ></Tag
        >
        <Tag class="flex" v-if="stuIntention" color="red"
          >{{ stuIntention }}</Tag
        >
        <span v-if="labelInfo.userLabelList.length">
          <Tag
            v-for="item in labelInfo.userLabelList"
            :key="item.id"
            color="default"
            class="flex"
            closable
            @on-close="delChongfu(item)"
            >{{ item.name }}</Tag
          >
        </span>
        <Button
          v-show="stuAllLabelInfos.length !== 0"
          style="cursor: pointer"
          class="flex"
          @click="addChongfu"
          icon="md-add"
          type="dashed"
          size="small"
          ></Button
        >
      </div>
    </Col>
    <Col
      span="12"
      class="stu-label"
      style="border-left: 1px solid rgba(0, 0, 0, 0.1); padding-left: 24px"
    >
      <span class="label" style="margin-right: 25px"></span>
      <Tag v-if="recentAttention.attentionDetail.grade" color="green">{{
        recentAttention.attentionDetail.grade
      }}</Tag>
      <Tag v-if="recentAttention.attentionDetail.subject" color="green">{{
        recentAttention.attentionDetail.subject
      }}</Tag>
      <Tag v-if="recentAttention.attentionDetail.classType" color="blue">{{
        recentAttention.attentionDetail.classType
      }}</Tag>
      <Tag
        v-if="recentAttention.attentionDetail.serviceCenterName"
        color="blue"
        >{{ recentAttention.attentionDetail.serviceCenterName }}</Tag
      >
    </Col>
    <!--  -->
    <Modal
      title=""
      v-model="showLabelModal"
      footer-hide
      :mask-closable="false"
    >
      <Row type="flex" justify="center">
        <Col>
          <span> &nbsp;&nbsp;&nbsp;</span>
          <Select multiple v-model="selectList" style="width: 300px">
            <Option
              v-for="item in stuAllLabelInfos"
              :value="item.id"
              :key="item.id"
              >{{ item.name }}</Option
            >
          </Select>
        </Col>
        <Col class="margin-top"> </Col>
        <Col class="margin-top">
          <Button class="confirm" @click="cancelModal"></Button>
          <Button class="confirm" type="primary" @click="saveLabel"
            ></Button
          >
        </Col>
      </Row>
    </Modal>
  </Row>
</template>

<script>
import {
  queryStudentLabel,
  getBringNewAbility,
  getHighTransformPotential,
  getStuIntention,
  updateUserLabel,
  queryUserLabel,
  getStuAllLabelInfos
} from '@/api/crmStudent'
import {
  recentAttentionOfStudent
} from '@/api/student'
import Cookie from 'js-cookie'

export default {
  name: 'tag',
  props: {
    cityCode: String,
    uid: Number,
    stuCode: String,
    studentId: String
  },
  data () {
    return {
      labelInfo: {
        userLabelList: []
      },
      bringNewAbility: 0,
      highTransformPotential: 0,
      stuIntention: '',
      recentAttention: {
        attentionDetail: {}
      },
      userLabelList: [],
      showLabelModal: false,
      selectList: [],
      stuAllLabelInfos: []
    }
  },
  methods: {
    queryStudentLabelHandler () {
      let req = {
        studentUid: this.uid
      }
      queryStudentLabel(req).then((res) => {
        this.labelInfo = res
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    getBringNewAbilityHandler () {
      let req = {
        cityCode: this.cityCode,
        uid: this.uid
      }
      getBringNewAbility(req).then((res) => {
        this.bringNewAbility = res
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    getHighTransformPotentialHandler () {
      let req = {
        areaCode: this.cityCode,
        pyUid: this.uid,
        stuCode: this.stuCode,
        studentId: this.studentId
      }
      getHighTransformPotential(req).then((res) => {
        this.highTransformPotential = res
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    getStuIntentionHandler () {
      let req = {
        uid: this.uid
      }
      getStuIntention(req).then((res) => {
        this.stuIntention = res
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    getRecentAttentionOfStudent () {
      let req = {
        cityCode: this.cityCode,
        areaCode: this.cityCode,
        uid: this.uid,
        stuCode: this.stuCode,
        studentId: this.studentId
      }
      recentAttentionOfStudent(req).then((res) => {
        this.recentAttention = res
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    queryUserLabelHandler () {
      queryUserLabel({
        areaCode: this.cityCode,
        studentUid: this.uid
      }).then((res) => {
        this.userLabelList = res
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
    },
    addChongfu () {
      this.queryGetStuAllLabelInfos()
      this.$nextTick(() => {
        this.showLabelModal = true
      })
    },
    delChongfu (item) {
      this.$Modal.confirm({
        title: '',
        content: ` <span style="color: green">${item.name}</span> ?`,
        onOk: () => {
          let req = {
            id: item.id,
            tagId: item.id,
            studentUid: this.uid,
            operateType: 3
          }
          updateUserLabel(req).then(() => {
            this.queryStudentLabelHandler()
            this.queryGetStuAllLabelInfos()
            this.$Message.success('')
          }).catch((err) => {
            this.$Message.error({ content: ':' + err.message, duration: 5 })
          })
        }
      })
    },
    initView () {
      this.queryStudentLabelHandler()
      this.getBringNewAbilityHandler()
      this.getHighTransformPotentialHandler()
      this.getStuIntentionHandler()
      this.getRecentAttentionOfStudent()
      this.queryUserLabelHandler()
      this.queryGetStuAllLabelInfos()
    },
    saveLabel () {
      let req = {
        tagIds: this.selectList.join(','),
        studentUid: this.uid,
        operateType: 1,
        empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
      }
      updateUserLabel(req).then(() => {
        this.queryStudentLabelHandler()
        this.$Message.success('')
        this.selectList = []
        this.queryGetStuAllLabelInfos()
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      })
      this.showLabelModal = false
    },
    cancelModal () {
      this.showLabelModal = false
      this.selectList = []
    },
    queryGetStuAllLabelInfos () {
      getStuAllLabelInfos({ studentUid: this.uid }).then(res => {
        this.stuAllLabelInfos = res
      }).catch((err) => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    }
  }
}
</script>

<style scoped lang="less">
.wrapper {
  padding: 0px 24px;
  height: 90px;
  margin-bottom: 25px;
  background: #ffffff;
  .stu-label {
    display: flex;
    align-items: center;
  }
  .label {
    font-size: 14px;
    line-height: 26px;
    color: rgba(0, 0, 0, 0.45);
    min-width: 60px;
  }
}
</style>
