<template>
  <Row>
    <Col>
      <Row type="flex" align="middle">
        <Col span="8" class="txt-right"></Col>
        <Col span="8">
          <Select v-model="assistantWxId" class="w150">
            <template v-for="item in assistantWxList" >
              <Option
                  v-if="item.loginStatus === 1"
                  :value="item.assistantWxId"
                  :key="item.assistantWxId"
                >{{ item.isWorkWechat ? item.nickName + '' : item.nickName  }}</Option>
              <Tooltip v-else :key="item.assistantWxId" style="width: 100%;" :content="item.isWorkWechat ? '':''" placement="right" transfer>
                <Option
                  :disabled="item.loginStatus === 0"
                  :value="item.assistantWxId"
                  style="color: gray;"
                >{{ item.isWorkWechat ? item.nickName + '' : item.nickName  }}</Option>
              </Tooltip>
            </template>
          </Select>
        </Col>
      </Row>
    </Col>
    <Col style="margin-top: 12px;">
      <Row type="flex" align="middle">
        <Col span="8" class="txt-right">{{ isWorkWechat ? '' : '/'}}</Col>
        <Col span="8">
          <Input class="w150" v-model="wxAccount" @input="handleInputWxAccount" />
        </Col>
        <Col span="8">
          <Button :loading="isLoading" type="primary" @click="submit"></Button>
        </Col>
      </Row>
    </Col>
  </Row>
</template>

<script>
import { bind } from '@/api/wxmanage'
export default {
  props: {
    uid: {
      type: [String, Number],
      default: ''
    },
    assistantWxList: {
      type: Array,
      default: () => {
        return []
      }
    },
    encodedPhone: {
      type: [Number, String],
      default: ''
    }
  },
  data () {
    return {
      assistantWxId: '', // id
      wxAccount: '',
      isLoading: false,
      encrypted: true
    }
  },
  mounted () {
    const onlineAssitant = this.assistantWxList.filter(item => item.loginStatus === 1)
    this.assistantWxId = onlineAssitant[0] ? onlineAssitant[0].assistantWxId : ''
    this.wxAccount = this.encodedPhone || ''
  },
  computed: {
    isWorkWechat () {
      return this.assistantWxList.find(a => a.assistantWxId === this.assistantWxId).isWorkWechat
    }
  },
  methods: {
    handleInputWxAccount () {
      if (this.encrypted && this.encodedPhone) {
        this.$nextTick(() => {
          this.wxAccount = ''
          this.encrypted = false
        })
      }
    },
    async bindWx () {
      try {
        if (!this.assistantWxId) {
          return this.$Message.warning({ content: '', duration: 5 })
        }

        if (!this.wxAccount) {
          if (this.isWorkWechat) {
            return this.$Message.warning({ content: '', duration: 5 })
          } else {
            return this.$Message.warning({ content: '', duration: 5 })
          }
        }
        if (this.isWorkWechat && !/(^\d{11}$)|(^\d{3}[a-zA-Z]{4}\d{4}$)/.test(this.wxAccount)) {
          return this.$Message.warning({ content: '', duration: 5 })
        }
        const params = {
          wxAccount: this.wxAccount,
          assistantWxId: this.assistantWxId,
          isWorkWechat: this.isWorkWechat,
          uid: this.uid,
          encrypted: this.encrypted
        }
        this.isLoading = true
        await bind(params)
        this.$emit('add-success')
        this.$Message.success({ content: '' })
      } catch (error) {
        this.$Message.error({ content: '' + error.message, duration: 5 })
      }

      this.isLoading = false
    },
    submit () {
      this.bindWx()
    }
  }
}
</script>

<style lang="less" scoped>
.txt-right {
  text-align: right;
  font-size: 14px;
}

.w150 {
  width: 150px;
}
</style>
