<template>
  <div class="wrapper">
    <template v-for="item in fastLinks.links">
      <div class="card" :key="item.path" v-if="!item.needStu || item.needStu && card" @click="routerTo(item)">
        <div class="icon" :class="item.icon"></div>
        <div class="text">
          {{ item.name }}
        </div>
      </div>
      <div class="card dis" :key="item.path" v-else>
        <div class="icon" :class="item.icon"></div>
        <div class="text" style="color: #ccc;">
          {{ item.name }}
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: 'fastLinks',
  props: {
    cityCode: String,
    card: String
  },
  data () {
    return {
      fastLinks: Object.freeze({
        links: [{
          path: 'applyWorkbench',
          multiPath: 'applyWorkbenchForMulti',
          icon: 'bao',
          name: ''
        },
        {
          path: 'scoreRecording',
          multiPath: 'scoreRecordingForMulti',
          icon: 'per',
          name: '',
          needStu: true
        },
        {
          path: 'ewallet',
          multiPath: 'ewalletForMulti',
          icon: 'epay',
          name: '',
          needStu: true
        },
        {
          path: 'attendanceCardMgt',
          multiPath: 'attendanceCardMgtForMulti',
          icon: 'att',
          name: '',
          needStu: true
        },
        {
          path: 'giftMgt',
          multiPath: 'giftMgtForMulti',
          icon: 'gift',
          name: '',
          needStu: true
        }],
        multiType: 'query' // params
      })
    }
  },
  methods: {
    routerTo (item) {
      // 
      // uid tab uid tab
      const isMulti = item.multiPath && this.card
      let name = item.multiPath && this.card ? item.multiPath : item.path
      let options = {
        name,
        query: {
          cityCode: this.cityCode,
          card: this.card
        }
      }
      if (item.isHref) {
        top.open(item.path + `?card=${this.card || ''}&cityCode=${this.cityCode || ''}`)
      } else {
        if (isMulti) {
          options['params'] = {
            id: this.card
          }
        }
        this.$router.push(options)
      }
    }
  }
}
</script>

