<template>
    <!--  -->
    <div>
        <DatePicker
            :disabled="!studentDetail.uid"
            clearable
            placeholder="            ->         "
            type="datetimerange"
            :editable="false"
            v-model="formData.time"
            style="width: 300px;margin-top: 24px;margin-bottom: 24px;"
            @on-change="optDateChange($event, 'startTime', 'endTime')"
            @on-ok="handleDateChangeOk" @on-clear="handleDateChangeClear"></DatePicker>
        <Select v-model="activeTalkType"
                :disabled="!studentDetail.uid"
                placeholder="" style="width:100px"
                clearable>
            <Option :value="item.id" v-for="(item, index) in talkTypes" :key="index">{{ item.name }}</Option>
        </Select>
        <Select v-model="formData.identityId"
               :disabled="!studentDetail.uid"
               placeholder="" style="width:100px"
               filterable clearable @on-change="handleAdvisoryChange">
            <Option :value="item.identityId" v-for="(item, index) in advisoryList" :key="index">{{ item.empName }}</Option>
        </Select>
        <Button v-show="studentDetail.uid" icon="md-add" @click="$emit('maintainRecordAdd')" type="primary"></Button>
        <template v-if="communicationRecordFiltered.length">
          <Timeline style="max-height: 600px;overflow-y: auto;padding-top: 5px">
            <TimelineItem v-for="(item, index) in communicationRecordFiltered" :key="index">
                <Icon type="ios-radio-button-on" slot="dot" color="#3b7eff"/>
                  <div class="flex_style">
                    <h5>{{ item.sortTypeName }}</h5>
                    <Button class="taskBtn" v-if="item.sortType === 'task' && item.isCallRecordDisplay === 1" type='primary' shape="circle" size='small' @click="showPhoneVideo(item)" ></Button>
                  </div>
                <span class="time" style="line-height: 24px;margin-bottom: 4px">{{ item.sortTime }}</span>
                <ImTalk v-if="item.sortType === 'im'" :data="item" />
                <TaskMaintain v-if="item.sortType === 'task'" :data="item"  @check-record="getAdvisorFollowOtherInfo"/>
                <DiagnosticMaintain v-if="item.sortType === 'diagnostic'" :data="item"  />
            </TimelineItem>
          </Timeline>
        </template>
        <p v-else style="font-size: 14px; color: rgba(0, 0, 0, 0.25);"></p>

        <!---->
        <maintain-record-detail
          v-if="maintainRecordDetailModal"
          :show="maintainRecordDetailModal"
          :data="maintainRecordDetailData"
          @close="maintainRecordDetailModal = false">
        </maintain-record-detail>
         <Modal
          v-model="showPhoneVideoModal"
          title=""
          footer-hide
          @on-cancel="cancel">
          <Table :data="phoneVideoData" :columns="phoneVideo" class="record-table">
            <template slot-scope="{ row, index }"  slot="hasRecord">
              <audio
                v-if="row.audioUrl"
                controls="controls">
                <source :src="row.audioUrl">
              </audio>
              <a v-else-if="row.hasRecord === 1" @click="getAudioUrl(row.uuid, index)"></a>
              <span v-else></span>
            </template>
          </Table>
        </Modal>
    </div>
</template>

<script>
import ImTalk from './imTalk'
import TaskMaintain from './taskMaintain'
import DiagnosticMaintain from './diagnosticMaintain'
import { getAdvisorFollowRecord, getAdvisorFollowOtherInfo, getCallHistoryList, queryCallrecordUrl } from '@/api/crmStudent'
import { getWorkOrderHistory } from '@/api/onlineAdvisor'
import { queryStuFollowDetail } from '@/api/diagnosis'
import { queryPerformanceConsult } from '@/api/user'
import { mapState } from 'vuex'
import MaintainRecordDetail from './maintainRecordDetail'
export default {
  name: 'CommunicationRecord',
  components: {
    ImTalk,
    TaskMaintain,
    DiagnosticMaintain,
    MaintainRecordDetail
  },
  props: {
    studentDetail: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    communicationRecordFiltered () {
      if (!this.activeTalkType) {
        return this.communicationRecord
      }
      return this.communicationRecord.filter(item => item.sortType === this.activeTalkType)
    },
    ...mapState({
      roleId: state => state.roleRelativeData.userInfo.crmRoleId,
      cityCode: state => state.roleRelativeData.userInfo.cityCode,
      deptId: state => state.roleRelativeData.userInfo.crmDeptId,
      empName: state => state.roleRelativeData.userInfo.empName,
      empNo: state => state.roleRelativeData.userInfo.empNo
    })
  },
  data () {
    return {
      formData: {
        time: '',
        startTime: '',
        endTime: '',
        identityId: '' // 
      },
      communicationRecord: [], // 
      talkTypes: [
        { id: 'im', name: 'IM' },
        { id: 'task', name: '' },
        { id: 'diagnostic', name: '' }
      ],
      activeTalkType: '',
      advisoryList: [], // 
      maintainRecordDetailModal: false, // flag
      maintainRecordDetailData: {}, // 
      showPhoneVideoModal: false,
      phoneVideo: [
        {
          title: '',
          key: 'advisorName'
        },
        {
          title: '',
          key: 'startTime'
        },
        {
          title: '',
          slot: 'hasRecord'
        }
      ],
      phoneVideoData: []
    }
  },
  watch: {
    roleId () {
      this.queryAdvisoryList()
    }
  },
  mounted () {
    this.$nextTick(() => {
      if (this.roleId) {
        this.queryAdvisoryList()
      }
    })
  },
  methods: {
    getAudioUrl (id, index) {
      queryCallrecordUrl({
        callRecordId: id,
        empNo: this.empNo,
        empName: this.empName
      }).then(data => {
        this.$set(this.phoneVideoData[index], 'audioUrl', data)
      }).catch(err => {
        this.$Message.error('' + err.message)
      })
    },
    // 
    showPhoneVideo (item) {
      getCallHistoryList({
        // followDetailId: '123456',
        // pyUid: '1738719074'
        followDetailId: item.id,
        pyUid: item.pyUid
      }).then(data => {
        this.phoneVideoData = data
        this.showPhoneVideoModal = true
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    cancel () {
      this.showPhoneVideoModal = false
    },
    // 
    getAdvisorFollowOtherInfo (item) {
      let obj = {
        crmIdentityId: item.identityId,
        followId: item.id,
        pyUid: item.pyUid
      }
      getAdvisorFollowOtherInfo(obj).then(data => {
        this.maintainRecordDetailData = item

        this.maintainRecordDetailData.otherInfo = data
        this.maintainRecordDetailModal = true
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    // 
    queryAdvisoryList () {
      queryPerformanceConsult({
        areaCode: this.cityCode,
        roleId: this.roleId,
        deptId: this.deptId
      }).then(data => {
        this.advisoryList = data
      }).catch(err => {
        this.$message.error('' + err.message)
      })
    },
    async initView (clear) {
      if (clear) {
        this.formData = {
          time: '',
          startTime: '',
          endTime: '',
          identityId: '' // 
        }
        this.activeTalkType = ''
      }
      const params = {
        cityCode: this.studentDetail.cityCode,
        pyUid: this.studentDetail.uid,
        pageNo: 1,
        pageSize: 30,
        ...this.formData,
        startTime: this.formData.startTime || undefined,
        endTime: this.formData.endTime || undefined

      }

      try {
        Promise.all(
          [
            await getWorkOrderHistory(params), // IM
            await getAdvisorFollowRecord(params), // 
            await queryStuFollowDetail(params) // 
          ]
        ).then(([res1, res2, res3]) => {
          res1.forEach(item => {
            item.sortType = 'im'
            item.sortTypeName = 'IM'
            item.sortTimeNumber = new Date(item.contactStartTime).getTime()
            item.sortTime = item.contactStartTime
          })

          res2.forEach(item => {
            item.sortType = 'task'
            item.sortTypeName = ''
            item.sortTimeNumber = new Date(item.createTime).getTime()
            item.sortTime = item.createTime
          })

          res3.forEach(item => {
            item.sortType = 'diagnostic'
            item.sortTypeName = ''
            item.sortTimeNumber = new Date(item.operateTime).getTime()
            item.sortTime = item.operateTime
          })

          let newArr = [...res1, ...res2, ...res3]

          newArr = newArr.sort((a, b) => b.sortTimeNumber - a.sortTimeNumber)

          this.communicationRecord = Object.freeze(newArr)
        })
      } catch (error) {
        this.$Message.error({ content: ': ' + error.message, duration: 5 })
      }
    },
    handleAdvisoryChange () {
      this.initView()
    },
    // 
    optDateChange (val, start, end) {
      this.formData[start] = val[0]
      this.formData[end] = val[1]
    },
    handleDateChangeClear () {
      this.formData.startTime = ''
      this.formData.endTime = ''
      this.initView()
    },
    handleDateChangeOk () {
      this.initView()
    }
  }
}
</script>

<style lang="less" scoped>
.time {
  color: rgba(0, 0, 0, 0.45);
  font-size: 12px;
}
.flex_style {
  display: flex;
  align-items: center;
  .taskBtn {
    margin-left: 20px;
    border-radius: 32px !important;
  }
}
  audio {
    width: 120px;
    height: 20px;
  }
  .record-table /deep/ .ivu-table-cell{
    padding-left: 16px;
    padding-right: 16px;
  }
</style>
