<template>
  <div>
    <Timeline v-if="dataList.length" style="max-height: 600px;overflow-y: auto;">
        <TimelineItem v-for="(v, k) in dataList" :key="k">
          <!-- <Icon type="ios-trophy" ></Icon> -->
          <Icon type="ios-radio-button-on" slot="dot" color="#3b7eff"/>
          <div class="transfer-record-bar">
              <div class="left">
                  <div class="icon" :class="transferRecordTypeMap[v.isFollow].icon"></div>
                  <span style="margin-top: 5px;">{{ transferRecordTypeMap[v.isFollow].name }}</span>
              </div>
              <div class="right">
                  <span style="color: rgba(0, 0, 0, 0.45);font-size: 12px;line-height: 24px;">{{ v.userName }} {{ v.createTime }}</span>
                  <span>{{ v.content }}</span>
              </div>
          </div>
        </TimelineItem>
    </Timeline>
    <span v-else style="font-size: 14px; color: rgba(0, 0, 0, 0.25);"></span>
  </div>

</template>

<script>
import {
  getStuBelongRecord
} from '@/api/crmStudent'
export default {
  name: 'TransferRecordBar',
  props: {
    studentDetail: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      dataList: [],
      transferRecordTypeMap: {
        2: {
          name: '',
          icon: 'transfer'
        },
        4: {
          name: '',
          icon: 'bind'
        }
      }
    }
  },
  methods: {
    initView () {
      this.dataList = []
      this.getStuBelongRecord()
    },
    getStuBelongRecord () {
      // 
      getStuBelongRecord({
        studentUid: this.studentDetail.uid
      }).then(res => {
        this.dataList = res
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    }
  }
}
</script>

