<template>
  <!--  -->
  <div>
    <Table
      v-if="tableData.length"
      border
      :columns="columns"
      :data="tableData"
      :maxHeight="300"
    >
    </Table>
    <p v-else style="font-size: 14px; color: rgba(0, 0, 0, 0.25);margin-top: 10px;"></p>
    <!--  -->
    <tal-class-detail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
      isDisplayExportBtn
    >
    </tal-class-detail>
  </div>
</template>

<script>
import CustomerMixin from '@/components/mixin/customerDetail'
import classDetailMixin from '@/mixins/classDetail'
import { auditionAppointmentList } from '@/api/audition'
export default {
  name: 'auditionReservationRecord',
  mixins: [CustomerMixin, classDetailMixin],
  props: {
    formData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      tableData: [],
      // 
      attendenceStatus: {
        0: '',
        1: '',
        2: ''
      },
      columns: Object.freeze([
        {
          title: '',
          key: 'planAdvisorName',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.planAdvisorName || '')
          }
        },
        {
          title: '',
          minWidth: 82,
          key: 'treatAdvisorName',
          align: 'left',
          render: (h, params) => {
            return h('span', params.row.treatAdvisorName || '')
          }
        },
        {
          title: '',
          key: 'stuIntention',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.stuIntention || '')
          }
        },
        {
          title: '',
          key: 'channel',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.channel || '')
          }
        },
        {
          title: '',
          key: 'createTime',
          align: 'left',
          minWidth: 105,
          render: (h, params) => {
            return h('span', params.row.createTime || '')
          }
        },
        {
          title: '',
          key: 'appointmentStatus',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.appointmentStatus === 5 ? '' : '')
          }
        },
        {
          title: '',
          key: 'isAttendance',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            const attendenceName =
              this.attendenceStatus[params.row.isAttendance] || '--'
            return h('span', attendenceName)
          }
        },
        {
          title: '',
          key: 'year',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.year || '--')
          }
        },
        {
          title: '',
          key: 'term',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.term || '--')
          }
        },
        {
          title: '',
          key: 'subject',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.subject || '--')
          }
        },
        {
          title: '',
          key: 'grade',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.grade || '--')
          }
        },
        {
          title: '',
          key: 'className',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return params.row.className
              ? h(
                'a',
                {
                  on: {
                    click: () => {
                      this.showClassDetail(params.row.classId)
                    }
                  }
                },
                params.row.className
              )
              : h('span', '--')
          }
        },
        {
          title: '',
          key: 'curriculumNum',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.curriculumNum || '--')
          }
        },
        {
          title: '',
          key: 'classTimeVal',
          align: 'left',
          minWidth: 105,
          render: (h, params) => {
            return h('span', params.row.classTimeVal || '--')
          }
        },
        {
          title: '',
          key: 'serviceCenter',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.serviceCenter || '--')
          }
        },
        {
          title: '',
          key: 'isRegist',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.isRegist === 1 ? '' : '')
          }
        },
        {
          title: '',
          key: 'isPay',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.isPay === 1 ? '' : '')
          }
        },
        {
          title: '',
          key: 'followCount',
          align: 'left',
          minWidth: 82,
          render: (h, params) => {
            return h('span', params.row.followCount)
          }
        }
      ])
    }
  },
  methods: {
    // 
    initView () {
      const params = {
        stuCode: this.formData.card,
        uid: this.formData.uid,
        cityCode: this.formData.cityCode,
        pageSize: 10,
        pageNo: 1
      }
      auditionAppointmentList(params)
        .then((res) => {
          this.tableData = Object.freeze(res.auditionAppointmentResps) || []
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
        .finally(() => {
          this.tableLoading = false
        })
    }
  }
}
</script>

