<template>
    <!--  -->
    <Table :maxHeight="300"  :columns="columns" :data="data">
      <template slot-scope="{ row }"  slot="classtimeNames">
        <Poptip
          trigger="hover"
          width="300"
          word-wrap
          :content="row.classtimeNames"
          transfer
        >{{getClasstimeNames(row.classtimeNames)}}</Poptip>
      </template>
    </Table>
</template>

<script>
import { Icon } from 'view-design'
import { copyText } from '@/utils/utils'

export default {
  name: 'ClassDetailTable',
  props: {
    data: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      classTypeMap: {
        1: '',
        2: '',
        4: ''
      },
      columns: [
        {
          title: '',
          key: 'className',
          fixed: 'left',
          minWidth: 150,
          render: (h, params) => {
            return h('a', {
              style: {
                color: '#3B7EFF'
              },
              on: {
                click: () => {
                  this.$emit('show-class-detail', params.row.classId)
                }
              }
            }, params.row.className)
          },
          renderHeader: (h) => {
            return h('span', {
              style: {
                color: '#90939a',
                'font-weight': 'normal'
              }
            }, '')
          }
        },
        {
          title: 'ID',
          key: 'registId',
          minWidth: 150,
          render: (h, params) => {
            return h('div', {
              style: {
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  copyText(params.row.registId)
                }
              }
            }, [
              h('span', params.row.registId || '--'),
              h(Icon, {
                props: {
                  type: 'ios-copy-outline'
                },
                style: {
                  'font-size': '18px'
                }
              })
            ])
          }
        },
        {
          title: '',
          key: 'classType',
          minWidth: 100,
          render: (h, params) => {
            return h('span', this.classTypeMap[params.row.classType])
          }
        },
        {
          title: '',
          key: 'subjectNames',
          minWidth: 100
        },
        {
          title: '',
          key: 'teacherNames',
          minWidth: 100,
          render: (h, params) => {
            const teacherNames = params.row.teacherNames.split(',')
            const teacherIds = params.row.teacherIds.split(',')
            const teachers = teacherNames.map((item, index) => {
              return {
                name: item,
                id: teacherIds[index]
              }
            })
            const teacherEelments = teachers.map((item, index) => {
              return h('a', {
                style: {
                  color: '#3B7EFF'
                },
                on: {
                  click: () => {
                    this.$emit('show-teacher-detail', item.id)
                  }
                }
              }, item.name + (index === teachers.length - 1 ? '' : ','))
            })

            return h('p', teacherEelments)
          }
        },
        {
          title: '',
          key: 'tutorRealName',
          minWidth: 100
        },
        {
          title: '',
          key: 'classroomName',
          align: 'center',
          minWidth: 100
        },
        {
          title: '',
          minWidth: 100,
          slot: 'classtimeNames'
        },
        {
          title: '',
          key: 'startDate',
          minWidth: 100,
          render: (h, params) => {
            return h('span', params.row.startDate.split(' ')[0])
          }
        },
        {
          title: '',
          key: 'endDate',
          minWidth: 100,
          render: (h, params) => {
            return h('span', params.row.endDate.split(' ')[0])
          }
        },
        {
          title: '',
          key: 'price',
          minWidth: 100
        }
      ]
    }
  },
  methods: {
    getClasstimeNames (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    }
  }
}
</script>
