<template>
  <!--  -->
  <div>
    <Table v-if="tableData.length" border :columns="columns" :data="tableData"></Table>
    <p v-else style="font-size: 14px; color: rgba(0, 0, 0, 0.25);margin-top: 10px;"></p>
  </div>
</template>

<script>
import CustomerMixin from '@/components/mixin/customerDetail'
import { getApplyList } from '@/api/appointmentApply'

export default {
  name: 'applyRecord',
  mixins: [CustomerMixin],
  props: {
    formData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      tableData: [],
      columns: [
        {
          title: '',
          key: 'maintainerName',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.maintainerName || '--')
          }
        },
        {
          title: '',
          key: 'applyChannel',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.applyChannel || '--')
          }
        },
        {
          title: '',
          key: 'referrerName',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.referrerName || '--')
          }
        },
        {
          title: '',
          key: 'createTime',
          align: 'left',
          minWidth: 105,
          render: (h, params) => {
            return h('span', params.row.createTime || '--')
          }
        },
        {
          title: '',
          key: 'year',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.year || '--')
          }
        },
        {
          title: '',
          key: 'gradeName',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.gradeName || '--')
          }
        },
        {
          title: '',
          key: 'subject',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.subject || '--')
          }
        },
        {
          title: '',
          key: 'className',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', this.classTypeMap[params.row.classType] || '--')
          }
        },
        {
          title: '',
          key: 'intendedServiceCenter',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.intendedServiceCenter || '--')
          }
        },
        {
          title: '',
          key: 'referrerStuName',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return params.row.referrerStuCode
              ? h(
                'a',
                {
                  on: {
                    click: () => {
                      const stu = {
                        card: params.row.referrerStuCode,
                        cityCode: params.row.referrerStuCityCode
                      }
                      this.goCustomerDetail(stu)
                    }
                  }
                },
                params.row.referrerStuName || ''
              )
              : h('span', params.row.referrerStuName || '')
          }
        },
        {
          title: '',
          key: 'applyStatus',
          align: 'left',
          minWidth: 80,
          render: (h, params) => {
            return h(
              'span',
              this.applyStatusMap[params.row.applyStatus] || '--'
            )
          }
        }
      ],
      applyStatusMap: {
        0: '',
        1: '',
        2: ''
      },
      classTypeMap: {
        1: '',
        2: '',
        4: ''
      }
    }
  },
  methods: {
    // 
    initView () {
      const params = {
        uid: this.formData.uid,
        stuCode: this.formData.card,
        isStudentPage: 1 // 
      }
      getApplyList(params)
        .then((res) => {
          this.tableData = Object.freeze(res.records) || []
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
    }
  }
}
</script>

<style lang="less" scoped>
</style>
