import axios from '@/libs/api.request'

/**
 * @ http://apidoc.speiyou.cn/api/view/17337#
 *
 * 
 *
*/
export function auditionAppointmentList (params) {
  return axios.request({
    url: 'audition/auditionAppointmentList',
    method: 'post',
    data: params
  })
}

