            align: 'center'
          }
        ]
      },
      inviteData: {
        invitees: {
          records: []
        }
      },
      inviteTableData1: [],
      inviteTableData2: [],
      trackArr: [
        { ref: 'diagnostic', trackId: '15-2932', tracked: false },
        { ref: 'oldNew', trackId: '15-2929', tracked: false },
        { ref: 'registNotPay', trackId: '15-2930', tracked: false },
        { ref: 'waitClass', trackId: '15-2931', tracked: false }
      ],
      // 
      demandPool: {
        data: []
      }
    }
  },
  mounted () {
    // 
    on(document.getElementById('root-wrapper' + this.t), 'scroll', this.throttle(() => {
      this.trackArr.forEach(item => {
        const bol = this.isInDisplayArea(this.$refs[item.ref])
        if (bol && !item.tracked) {
          item.tracked = true
          XesAnalytics.track(item.trackId, {
            adviser: this.roleId,
            adviser_departmentID: this.$store.state.roleRelativeData.userInfo.crmDeptId,
            // adviser_departmentNAME: '',
            adviser_empNo: this.$store.state.roleRelativeData.roleInfo.userWorkCode,
            adviser_citycode: this.$store.state.roleRelativeData.userInfo.cityCode,
            userid: this.stuInfo.uid
          })
        }
      })
    }, 200))
  },
  beforeDestroy () {
    off(document.getElementById('root-wrapper' + this.t), 'scroll', this.throttle(() => {
      this.trackArr.forEach(item => {
        console.log(item.ref)
        const bol = this.isInDisplayArea(this.$refs[item.ref])
        if (bol && !item.tracked) {
          item.tracked = true
          XesAnalytics.track(item.trackId, {
            adviser: this.roleId,
            adviser_departmentID: this.$store.state.roleRelativeData.userInfo.crmDeptId,
            // adviser_departmentNAME: '',
            adviser_empNo: this.$store.state.roleRelativeData.roleInfo.userWorkCode,
            adviser_citycode: this.$store.state.roleRelativeData.userInfo.cityCode,
            userid: this.stuInfo.uid
          })
        }
      })
    }, 200))
  },
  methods: {
    // 
    async getDemandPool () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          studentCard: this.stuDetail.card,
          pageSize: 30
        }
        const res = await queryAssembleClassDemandPage(params)
        this.demandPool.data = Object.freeze(res.records) || []
      } catch (error) {
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      }
    },
    updateStuSuccess () {
      this.getStudentLabelBaseInfoHandler()
    },
    getClasstimeNames (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    },
    maintainRecordAdd () {
      this.$refs.notice.maintainRecordAdd('1')
    },
    maintainRecordAddSuccess () {
      this.$refs.communicationRecord.initView()
      this.getStuTaskNotice()
    },
    // 
    throttle (fn, wait) {
      var pre = Date.now()
      return function () {
        var context = this
        var args = arguments
        var now = Date.now()
        if (now - pre >= wait) {
          fn.apply(context, args)
          pre = Date.now()
        }
      }
    },
    // 
    isInDisplayArea (ref) {
      const windowHeight = window.innerHeight || document.documentElement.clientHeight
      const elementTop = ref.getBoundingClientRect().top
      const bol = elementTop + windowHeight / 5 < windowHeight && elementTop - windowHeight / 5 > 0
      return bol
    },
    // 
    async getWaitClass () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          studentId: this.stuDetail.studentId
        }
        const data1 = await queryWaitClassGeneral(params)
        const data2 = await queryWaitClassList(params)
        let reading = []
        let notReading = []
        data1.subjectRegs.forEach(item => {
          if (item.isReg === 1) {
            reading.push(item.subjectName)
          } else {
            notReading.push(item.subjectName)
          }
        })
        this.waitClass = Object.assign(this.waitClass, data1,
          { data: data2 || [], reading: reading.join(''), notReading: notReading.join('') }
        )
      } catch (error) {
        this.$Message.error({ content: ': ' + error.message, duration: 5 })
      }
    },
    // 
    async getNotPayClassList () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          studentId: this.stuDetail.studentId
        }
        const data = await getNotPayClassList(params)
        this.registNotPay = Object.assign(this.registNotPay, data, {
          noPayClassList: data.noPayClassList || []
        })
      } catch (error) {
        this.$Message.error({ content: ': ' + error.message, duration: 5 })
      }
    },
    // 
    async getUserSource () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          stuId: this.stuDetail.studentId
        }
        const data = await getUserSource(params)
        if (data) {
          this.mofangActivity.data = [Object.freeze(data)]
        } else {
          this.mofangActivity.data = []
        }
        this.mofangActivity.loaded = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // -
    async queryStudentDiagnosisForAdvisor () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          pageSize: 10,
          pyUid: this.stuDetail.uid
        }
        const data = await queryStudentDiagnosisForAdvisor(params)

        this.diagnosisClass.data = Object.freeze(data.studentDiagnosisRespList)
        this.diagnosisClass.diagnosisClassCount = data.diagnosisClassCount
        this.diagnosisClass.diagnosisSubjectNames = data.diagnosisSubjectName.join(',')
        this.diagnosisClass.loaded = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 
    async getActiveClass () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          pageSize: 100,
          termId: 6,
          studentId: this.stuDetail.studentId,
          mode: 4 // 0,1,4,8
        }
        const data = await queryRegisterClassList(params)

        this.activeClass.data = Object.freeze(data.records)
        this.activeClass.loaded = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 
    async getGurrentReadingCondition () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          pageSize: 100,
          termList: [1, 2, 3, 4],
          studentId: this.stuDetail.studentId,
          mode: 4 // 0,1,4,8
        }
        // 
        queryRegisterClassGeneral(params).then(res => {
          this.currentReadingCondition.totalCount = res.totalCount
          this.currentReadingCondition.subjectPays = res.subjectPays
        }).catch(error => {
          this.$Message.error({ content: error.message, duration: 5 })
        })

        const data = await queryRegisterClassList(params)

        this.currentReadingCondition.data = Object.freeze(data.records)
        this.currentReadingCondition.loaded = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 
    async getShortClass () {
      try {
        const params = {
          cityCode: this.stuDetail.cityCode,
          pageSize: 100,
          termId: 5,
          studentId: this.stuDetail.studentId,
          mode: 4 // 0,1,4,8
        }
        const data = await queryRegisterClassList(params)

        this.shortClass.data = Object.freeze(data.records)
        this.shortClass.loaded = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    getStuTaskNotice () {
      if (this.roleId === 5) { // 
        stuTaskNotice({
          pyUid: this.stuDetail.uid
        }).then(res => {
          this.taskNotice = res
        }).catch(err => {
          this.$Message.error({
            content: '' + err.message,
            duration: 5
          })
        })
      }
    },
    getStudentLabelBaseInfoHandler () { // 
      getStudentLabelBaseInfo({
        studentUid: this.stuDetail.uid,
        cityCode: this.stuDetail.cityCode
      }).then(res => {
        this.stuInfo = res
        this.$refs.userCard.judgeDispatchStudentToSelfHandler()
        this.$refs.stuInfo.initView()
      }).catch(err => {
        this.stuInfo = {}
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    showClassDetailHandel (classId) {
      this.showClassDetail(classId)
    },
    showTeacherDetailHandel (teacherId) {
      this.showTeacherDetail(this.stuDetail.bindingCode, teacherId)
    },
    setStuInfo (stu) {
      this.stuDetail = stu
      this.getStuTaskNotice()
      this.getStudentLabelBaseInfoHandler()
      this.getActivityUserInviteHandler()
      this.$nextTick(() => {
        this.$refs.tags.initView()
        this.$refs.transferRecord.initView()
        this.$refs.communicationRecord.initView(true)
        this.getShortClass()
        this.getGurrentReadingCondition()
        this.getActiveClass()
        this.queryStudentDiagnosisForAdvisor()
        this.getUserSource()
        this.getNotPayClassList()
        this.getWaitClass()
        this.getDemandPool()
        this.$refs.auditionReservationRecord.initView()
        this.$refs.auditionApplyRecord.initView()
      })
    },
    sendMsgEv () {
      const account = this.stuInfo.studentCrmFeature.nimAccid
      if (!account) {
        this.$message({
          message: 'ID',
          type: 'error'
        })
        return
      }
      if (this.$store.state.imchat.status === 'sucess') {
        this.$store.dispatch('imchat/startSession', account)
      } else {
        this.$message({
          message: 'im',
          type: 'error'
        })
      }
    },
    editStu () {
      if (this.stuDetail.uid) {
        this.$refs.stuSearch.enterDetail()
      } else {
        this.$Message.warning('')
      }
    },
    getActivityUserInviteHandler () {
      getActivityUserInvite({
        cityCode: this.stuDetail.cityCode,
        uid: this.stuDetail.uid,
        pageNo: 1,
        pageSize: 10
      }).then(data => {
        this.inviteData = data
        this.inviteTableData1 = data.inviter && data.inviter.realName ? [data.inviter] : []
        this.inviteTableData2 = data.invitees.records
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    }
  },
  watch: {
    noticeShow (n) { // scroll-offset
      this.anchorRerender = true
      const t = setTimeout(() => {
        this.anchorRerender = false
        clearTimeout(t)
      }, 100)
    }
  },
  computed: {
    ...mapState({
      roleId: state => state.roleRelativeData.userInfo.crmRoleId
    }),
    // 
    ifConsultantRole () {
      const crmRoleId = this.roleId
      const identityId = this.$store.state.roleRelativeData.roleInfo.identityId
      return crmRoleId === 5 && !!this.stuInfo.studentCrmFeature.nimAccid && Number(identityId) === Number(this.stuInfo.studentCrmFeature.belongIdentityId)
    },
    noticeShow () { // 
      return this.taskNotice.taskCount - this.taskNotice.diagnosisCount - this.taskNotice.auditionCount > 0
    },
    udcCallShow () { // 
      return this.$store.state.roleRelativeData.roleIds.includes(this.roleId)
    },
    fastLinkStickyStyle () {
      let top = 0
      if (this.noticeShow) {
        top += 50
      }
      // if (this.$store.state.roleRelativeData.roleIds.includes(this.roleId)) {
      //   top += 44
      // }
      return `position: sticky;top:${top}px`
    },
    anchorStickyStyle () {
      let top = 58
      if (this.noticeShow) {
        top += 50
      }
      // if (this.$store.state.roleRelativeData.roleIds.includes(this.roleId)) {
      //   top += 44
      // }
      return `position: sticky;top:${top}px`
    }
  }
}
</script>

