<template>
    <div class="wrapper">
      <div class="notice">
        <Icon color="#e6a23c" type="ios-alert" style='margin-left:24px;margin-right:8px' :size="16"/>
        <span></span>
        <a @click="maintainRecordAdd('0')"></a>
      </div>
      <div class="margin"></div>
      <MaintainRecordDetail
        :show="maintainRecordAddModal"
        :uid="uid"
        @close="maintainRecordAddModal = false"
        @success="addSuccess"
        :data="maintainRecordAddData"
        :from="from"
        ref="addrecord">
      </MaintainRecordDetail>
    </div>
</template>

<script>
import {
  getStudentNeedFollowTask,
  addFollowRecord
} from '@/api/crmStudent'
import MaintainRecordDetail from './maintainRecordAdd'
export default {
  name: 'Notice',
  components: {
    MaintainRecordDetail
  },
  props: {
    uid: Number
  },
  data () {
    return {
      maintainRecordAddModal: false,
      maintainRecordAddData: {},
      from: '0' // 0  1 
    }
  },
  methods: {
    maintainRecordAdd (from) {
      this.from = from
      this.maintainRecordAddModal = true
      getStudentNeedFollowTask({
        studentUid: this.uid
      }).then(data => {
        this.maintainRecordAddData = data
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    addSuccess (params) { // 
      let obj = params
      obj.identityId = ''
      obj.pyUid = this.uid
      addFollowRecord(obj).then(() => {
        this.maintainRecordAddModal = false
        this.$Message.success({
          content: '',
          duration: 5
        })
        this.$emit('maintainRecordAddSuccess')
        this.$refs.addrecord.setValue()
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
  .wrapper {
    position:sticky;
    top:0px;
    z-index: 1000;
    .notice {
      display: flex;
      align-items: center;
      height: 40px;
      background: #FFFBE6;
      border: 1px solid #FFE58F;
      border-radius: 2px;
      font-size: 14px;
    }
    .margin {
      height: 10px;
      background: #f4f7f9;
    }
  }

</style>
