<template>
  <div class="wrapper">
    <div class="stu-info">
      <div v-if="stuInfo.stuSex === undefined" class="avatar">
        <Icon type="md-person" :size="30" color="#fff"/>
      </div>
      <Sex v-else :sex="stuInfo.stuSex"></Sex>
      <div class="info">
        <div class="top">
          <span class="name">{{ stuInfo.stuRealName || '--' }}</span>
          <Icon v-if="uid && callShow" custom="iconfont icon-phone-fill" style="color: white;font-size: 24px;cursor: pointer" @click="$store.state.app.callInstance.call(cityCode, uid)"></Icon>
          <Button v-if="canBindStu" icon="ios-link" size="small" @click="dispatchStudentToSelfHandler"></Button>
          <Button v-if="stuInfo.isStuAccountRelate" @click="openRelatedStu" size="small" style="margin-left:10px"></Button>
        </div>
        <div class="no">
           {{ stuInfo.stuLoginname || '--' }}
        </div>
      </div>
    </div>
    <div class="option">
      <div class="btn" @click="$emit('editStu')">
        <div class="icon edit"></div>
        <div class="text"></div>
      </div>
      <div class="btn" @click="printQR">
        <div class="icon printer"></div>
        <div class="text"></div>
      </div>
    </div>
    <Modal
      :mask-closable="false"
      title=""
      width="560"
      v-model="showRelatedStu"
      :footer-hide="true"
    >
      <RelatedStu v-if="showRelatedStu"
                  :city-code="cityCode"
                  :uid="uid"
                  :student-id="stuInfo.stuId"
                  @close="showRelatedStu = false"></RelatedStu>
    </Modal>
  </div>
</template>

<script>
import RelatedStu from '_c/archives/relatedStu'
import Sex from '_c/archives/sex'
import {
  dispatchStudentToSelf, judgeDispatchStudentToSelf
} from '@/api/crm'
export default {
  name: 'userCard',
  components: {
    RelatedStu,
    Sex
  },
  props: {
    cityCode: String,
    uid: Number,
    stuInfo: Object
  },
  data () {
    return {
      showRelatedStu: false,
      canBindStu: false,
      isBinding: false,
      bindStuParams: {}
    }
  },
  computed: {
    callShow () { // 
      const callGrayCity = this.$store.state.app.callGrayCity
      if ((!callGrayCity || !callGrayCity.length || callGrayCity.includes(this.cityCode)) && this.$store.state.roleRelativeData.roleIds.includes(this.$store.state.roleRelativeData.userInfo.crmRoleId)) {
        return true
      }
      return false
    }
  },
  methods: {
    judgeDispatchStudentToSelfHandler () {
      judgeDispatchStudentToSelf({
        uid: this.uid,
        cityCode: this.cityCode
      }).then(res => {
        this.canBindStu = res.bindingStatus
        if (res.bindingStatus) {
          this.bindStuParams = res
        }
      }).catch(err => {
        this.canBindStu = false
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    dispatchStudentToSelfHandler () {
      this.isBinding = true
      dispatchStudentToSelf(this.bindStuParams).then(() => {
        this.canBindStu = false
        this.$Message.success({
          content: '',
          duration: 5
        })
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.isBinding = false
      })
    },
    openRelatedStu () {
      this.showRelatedStu = true
    },
    printQR () {
      if (this.stuInfo.attendQRCode) {
        top.open(`/service/printQR?code=${this.stuInfo.attendQRCode}&name=${this.stuInfo.stuRealName}&grade=${this.stuInfo.stuGradeName}&stuNo=${this.stuInfo.stuLoginname}&print=true`)
      } else {
        this.$Message.warning('')
      }
    }
  }
}
</script>

